var self;
export default {
	onLoad() {
		self=this;
	},
	onShow() {
		self=this;
		uni.getStorage({
		    key: 'portrait',
		    success: function (res) {
				console.log(res.data)
		       self.portrait= res.data;
		    }
		});
	},
	data() {
		return {
			portrait:"../../static/166.jpg",
			p:["../../static/166.jpg"]
		}
	},
	methods: {
		clickPortrait:()=>{
			uni.showActionSheet({
			    itemList: ['查看头像大图', '从相册选取'],
			    success: function (res) {
			        if(res.tapIndex==0){
						self.p[0]=self.portrait;
						uni.previewImage({
							urls: self.p
						        });
					}
					if(res.tapIndex==1){
						console.log("选择照片")
						uni.chooseImage({
						    count: 1,
						    success: function (res) {
						        self.portrait= res.tempFilePaths[0];
								uni.setStorage({
									key: 'portrait',
									data: res.tempFilePaths[0],
									success: function (data) {
										console.log(data);
									}
								});
						    }
						});
					}
			    }
			});

		}
	}
}
